:root {
  letter-spacing: -.02em;
  color: #242424;
  font-synthesis: none;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29em;
}

body {
  padding: 100px 20px 20px;
}

.header {
  height: 80px;
  width: 100%;
  background-color: #00767a;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .5), 0 8px 10px rgba(0, 0, 0, .3);
}

.input-search {
  height: 40px;
  width: 280px;
  text-align: center;
  border: 0 solid gray;
  border-radius: 5px;
  margin-right: 16px;
  font-family: Montserrat, sans-serif;
}

.btn-search {
  height: 40px;
  text-transform: uppercase;
  color: #fff;
  letter-spacing: .05em;
  background-color: #004b55;
  border: 0 solid gray;
  border-radius: 5px;
  padding: 0 40px;
  font-family: Montserrat, sans-serif;
  font-weight: 700;
}

.load-more-hidden {
  display: none;
}

.load-more {
  height: 40px;
  text-transform: uppercase;
  border: 0 solid gray;
  border-radius: 5px;
  margin: 20px auto;
  padding: 0 40px;
  font-family: Montserrat, sans-serif;
  font-weight: 700;
}

.gallery {
  flex-flow: wrap;
  gap: 20px;
  display: flex;
}

.gallery-item {
  width: calc(50% - 10px);
}

@media only screen and (min-width: 1024px) {
  .gallery-item {
    width: calc(33.3333% - 13.3333px);
  }
}

@media only screen and (min-width: 1400px) {
  .gallery-item {
    width: calc(25% - 15px);
  }
}

.gallery-item-image {
  width: 100%;
  height: 250px;
  object-fit: cover;
  object-position: center;
  justify-content: center;
  align-items: center;
  display: flex;
}

.gallery-item-stats {
  text-align: center;
  background-color: #eee;
  justify-content: space-between;
  align-items: center;
  padding: 16px 8px;
  display: flex;
}

.item-stat p, h6 {
  font-size: 14px;
}

